html {
  box-sizing: border-box; }

.visually-hidden {
  display: none; }

body {
  background-color: red;
  min-width: 320px;
  margin: 0 auto;
  padding-bottom: 70px;
  background-color: white;
  background-color: rgba(229, 229, 229, 0.3);
  font-family: "Ubuntu", "Arial", sans-serif; }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1 {
  margin: 0;
  padding: 0; }

.container {
  margin: 0 auto;
  padding: 0 60px;
  max-width: 1200px; }

.version {
  color: gray;
  font-size: 12px; }

header {
  box-shadow: 0px 10px 20px rgba(51, 51, 51, 0.2);
  margin-bottom: 22px;
  background-color: white; }

.header__line {
  display: flex;
  justify-content: space-between;
  padding: 26px 119px; }

.line__logo {
  width: 90px;
  height: 23px;
  border-radius: 2px; }

.line__input {
  width: 424px;
  height: 28px;
  padding: 6px 18px;
  border: 1px solid #EBEBEB;
  box-sizing: border-box;
  border-radius: 4px;
  font-style: italic; }

.line__profile {
  width: 174px;
  height: 40px;
  border: 1px solid #EBEBEB;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #EBEBEB;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: gray; }

.navigation {
  list-style: none;
  color: black;
  text-decoration: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  font-size: 24px;
  color: gray; }

.navigation__item {
  margin-right: 22px;
  cursor: pointer;
  color: rgba(51, 51, 51, 0.8);
  font-size: 16px;
  line-height: 24px; }
  .navigation__item + .selected {
    border-bottom: 2px solid blue;
    color: black; }

.market__filters {
  padding: 0 134px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px; }

.filter__quotes,
.filter__time {
  width: 100px;
  height: 28px;
  background: #FFFFFF;
  border: 1px solid #1599D5;
  border-radius: 4px;
  font-size: 12.8px;
  line-height: 19px; }

.market__marketplace {
  padding-left: 134px; }

.block-item {
  width: 200px;
  height: 134px;
  background: #FFFFFF;
  padding: 16px;
  box-shadow: 5px 10px 20px rgba(51, 51, 51, 0.1);
  border-radius: 8px;
  margin-right: 23px;
  cursor: pointer; }

.market-item__title {
  margin: 0;
  font-size: 16px;
  line-height: 24px; }

.block-item__name {
  margin: 0;
  margin-bottom: 14px;
  font-size: 16px;
  color: #1599D5; }

.block-item__price {
  display: inline-block;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 16px; }

.block-item__rate,
.block-item__rate2 {
  font-size: 10.24px;
  line-height: 18px;
  display: flex;
  justify-content: space-between; }

.block-item__rate--qty {
  color: #CF0254; }
  .block-item__rate--qty + .green {
    color: green; }

.incide__blocks {
  width: 100%;
  height: 185px;
  display: flex; }
